.App {
    text-align: center;
    background-color: #3aafa9;
}

.Top-Bar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    min-height: calc(7.5vh);
    -ms-flex-align: center;
        align-items: center;
}
.PostContent img {
    width: 25px;
    height: 25px;
}
.ScrollView {
    overflow: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    max-height: calc(100vh - 50px);
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.HistoryLink {
    cursor: pointer;
}

.Link {
    text-decoration: none;
    color: white;
}
.ShareLabel {
    cursor: pointer;
}
.CommentsLink {
    text-decoration: none;
    color: black;
}

.Top-Bar-Element {
    cursor: pointer;
    font-family: 'Avenir';
    color: white;
    font-size: x-large;
}

.Top-Bar-Element:hover {
    color: #222222;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

::-webkit-scrollbar {
    display: none;
}
.background{
    background: -webkit-gradient(linear, right top, left top, from(#00f0b2), color-stop(#a8d4c8), to(#2a05f3));
    background: -webkit-linear-gradient(right, #00f0b2, #a8d4c8, #2a05f3);
    background: -o-linear-gradient(right, #00f0b2, #a8d4c8, #2a05f3);
    background: linear-gradient(270deg, #00f0b2, #a8d4c8, #2a05f3);
    -webkit-animation-name: AnimationName;
            animation-name: AnimationName;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    width: 100%;
    height: 100%;
}

@-webkit-keyframes AnimationName {
    0%{background-position:0 50%}
    50%{background-position:100% 50%}
    100%{background-position:0 50%}
}
@keyframes AnimationName {
    0%{background-position:0 50%}
    50%{background-position:100% 50%}
    100%{background-position:0 50%}
}



